import React, { Component } from "react";
import NavBar from '../NavBar/NavBar';
import TopBar from '../TopBar/TopBar';
import Style from '../style.css';
import Collapsible from '../Collapsible/Collapsible';
import FilterColumn from '../FilterColumn/FilterColumn';
import Footer from '../Footer/Footer';
import FlatButton from '../FlatButton/FlatButton';
import ResultCard from '../ResultCard/ResultCard';
import Placeholder from '../../img/card3.png';
import ChevronDown from './chevron-arrow-down.svg';
import ChevronUp from './chevron-arrow-up.svg';
import RadioActive from './radio_active.png';
import RadioDefault from './radio_default.png';
import CheckActive from './check-active.png'
import CheckDefault from './check-default.png'
import Tick from './tick.svg';
import SearchIcon from './search.png';
import Spinner from 'react-spinkit';
import FilterData from './filter_data2.json'
import FilterHeaders from './filter_headers.json'

const centerText = {
  position: 'relative',
  left: '50%'
}

class Services extends Component {

  constructor(props) {
    super(props);

    this.state = {
      hasSearched: false,
      viewingProfile : false ,
      searchResults: [],
      filters: FilterHeaders,
      isSearching: false,
      searchProviderLocation: '',
      searchProviderValue: ''
    }
  }

  myFilter = (key) => {
    return (element) => {
      let currentFilters = FilterData[key];
      if (!currentFilters) {
        console.log("NO Filters");
        return true;
      }
      if (currentFilters.Programme) {
        let filterKeys = currentFilters["Programme"].split(",").map(item => item.trim());;
        let none = true;
        for (let filterKey in filterKeys) {
          if (element.Programme == filterKeys[filterKey]) {
            none = false; 
          }   
        }
        if (none == true)
          return false;
      }
        
      if (currentFilters["Service Type"]) {
        let filterKeys = currentFilters["Service Type"].split(",").map(item => item.trim());;
        let elementSubTypes = currentFilters["Service_Type"].split(",").map(item => item.trim());;
        let none = true;
        for (let filterKey in filterKeys) {
          for (let elementSubType in elementSubTypes) {
            if (elementSubTypes[elementSubType] == filterKeys[filterKey]) {
              none = false; 
            }   
          }
        }
        if (none == true)
          return false;
      }

      if (currentFilters["Service_Sub_Types"]) {
        let filterKeys = currentFilters["Service sub-type"].split(",").map(item => item.trim());;
        let elementSubTypes = currentFilters["Service_Sub_Types"].split(",").map(item => item.trim());;
        let none = true;
        for (let filterKey in filterKeys) {
          for (let elementSubType in elementSubTypes) {
            if (elementSubTypes[elementSubType] == filterKeys[filterKey]) {
              none = false; 
            }   
          }
        }
        if (none == true)
          return false;
      }
              
      console.log(element);
      return true;
    }  
  }

  applyFilters = () => {
    let tempState = this.state; 
    let filteredResults = tempState.unfilteredResults
    tempState.searchResults = filteredResults;
    for(var key in tempState.filters) {
      if(tempState.filters[key]) {
        tempState.searchResults = filteredResults.filter(this.myFilter(key));
      }
      filteredResults = tempState.searchResults;
    }
    tempState.hasSearched = true;
    this.setState(tempState);
  }

  filterMethod = (key, val) => {
    let tempState = this.state;
    tempState.filters[key] = val;
    tempState.isSearching = false;
    this.setState(tempState);
    if(tempState.unfilteredResults)
      this.applyFilters();
  }

  searchProviders = () => {
    let self = this;
    let tempState = this.state;
    tempState.isSearching = true;
    this.setState(tempState);
    fetch('http://13.58.91.209:1337/findProvider3', {
      method: 'POST',
      headers: {
        'Accept': 'application/json',
        'Content-Type': 'application/json',
      },
      body: JSON.stringify({
        $keyword: '%' + self.state.searchProviderValue + '%',
        $location: '%' + self.state.searchProviderLocation + '%'
      })
    }).then(function(response) {
      if (response.status >= 400) {
        console.error("Bad response form server");
      } else {
        return response.json(); 
      } 
    }).then(function(data) {
      if (data.length == 0) {
        tempState.searchResults = [];
        tempState.unfilteredResults = [];
        tempState.hasSearched = true;
        tempState.isSearching = false;
        self.setState(tempState);
      } else {
        tempState.unfilteredResults = data;
        tempState.searchResults = data;
        self.setState(tempState);
        self.filterMethod();
      } 
    })
  }
  
  inputChanged = (e) => {
    let tempState = this.state;
    tempState[e.target.name] = e.target.value;
    this.setState(tempState);
  }

  viewProfile = (profile) => {
    console.log(this.state.searchResults[profile.index]);
    this.setState({
      viewingProfile: !this.state.viewingProfile,
      title: profile.title,
      business: profile.business,
      phone : profile.phone,
      description: profile.children,
      detail: 'services'
    });
  }

  changeTab = (tab) => {
    let tempState = this.state;
    tempState.detail = tab;
    this.setState(tempState);
  }

  render() {
    return ( 
    <div>

        <TopBar> </TopBar>
        <div className="nav-bar"> <NavBar> </NavBar> </div>
        <div className="service-header"></div>
        {
          this.state.viewingProfile ? 
          <div className="container">
            {/* -------------------- Start Profile View ----------------- */}
            <button className="return-results-button" onClick={this.viewProfile}> Return to results </button>
            
            <div>

              <div className="results-flex">
                <div className="width-60">
                  <div className="business-title">  {this.state.title}</div>
                  <div className="business-name"> {this.state.business}</div>
                  <div className="business-description">{this.state.description}</div>
                  <div className="business-title"> Ph. {this.state.phone}</div> 
                  <button className="business-website-button"> View Website</button>
                  <br></br>
                  <hr></hr>
                  <div className="flex-space">
                    <div>
                      Availability
                    </div>
                    <div className="circle-icon small-size"><img className="tick-icon" src={Tick}/></div>
                  </div>
                  <div className="flex-space">
                    <div>
                      Commonwealth Government subsidised
                    </div>
                    <div className="circle-icon small-size"><img className="tick-icon" src={Tick}/></div>
                  </div>
                </div>

                <div >
                  <img className="profile-map" src={Placeholder}/>
                </div>
              </div>
                
              {this.state.detail == "services" &&
              <div>
                <div className="details-tabs">
                  <div className="selected-tab"> Services </div>
                  <div className="unselected-tab" onClick={() => this.changeTab('costs')}> Costs </div>
                  <div className="unselected-tab" onClick={() => this.changeTab('contact')}> Contact Details </div>
                </div>

                <div className="profile-details-box">
                  <div className="padding-10">

                    <div className="details-row">
                      <img src={ChevronUp} className="detail-chevron"/>
                      <div className="circle-icon small-size"><img className="tick-icon" src={Tick}/></div>
                      <div>
                        <div className="details-heading"> Community Access - Individual Respite </div>
                        <div className="business-description">
                          Community access respite provides activities to encourage a sense of independence and social interaction and provides your carer with a break. This is provided to you individually. 
                        </div>
                       </div>
                    </div>

                    <hr></hr>

                    <div className="details-row">
                      <img src={ChevronDown} className="detail-chevron"/>
                      <div className="circle-icon small-size"><img className="tick-icon" src={Tick}/></div>
                      <div>
                        <div className="details-heading"> In-home Day Respite</div>
                      </div>
                    </div>

                    <hr></hr>

                    <div className="details-row">
                      <img src={ChevronDown} className="detail-chevron"/>
                    <div className="circle-icon small-size"><img className="tick-icon" src={Tick}/></div>
                      <div>
                        <div className="details-heading"> In-home Overnight Respite</div>
                      </div>
                    </div>

                  </div>
                </div>
              </div>
              }
              {this.state.detail == "costs" &&
              <div>
                <div className="details-tabs">
                  <div className="unselected-tab" onClick={() => this.changeTab('services')}> Services </div>
                  <div className="selected-tab" > Costs </div>
                  <div className="unselected-tab" onClick={() => this.changeTab('contact')}> Contact Details </div>
                </div>

                <div className="profile-details-box">
                  <div className="padding-10">

                    <div className="details-row">
                      <img src={ChevronUp} className="detail-chevron"/>
                      <div className="circle-icon small-size"><img className="tick-icon" src={Tick}/></div>
                      <div>
                        <div className="details-heading"> Community Access - Individual Respite </div>
                        <div className="business-description">
                          Community access respite provides activities to encourage a sense of independence and social interaction and provides your carer with a break. This is provided to you individually. 
                        </div>
                       </div>
                    </div>

                    <hr></hr>

                    <div className="details-row">
                      <img src={ChevronDown} className="detail-chevron"/>
                      <div className="circle-icon small-size"><img className="tick-icon" src={Tick}/></div>
                      <div>
                        <div className="details-heading"> In-home Day Respite</div>
                      </div>
                    </div>

                    <hr></hr>

                    <div className="details-row">
                      <img src={ChevronDown} className="detail-chevron"/>
                      <div className="circle-icon small-size"><img className="tick-icon" src={Tick}/></div>
                      <div>
                        <div className="details-heading"> In-home Overnight Respite</div>
                      </div>
                    </div>

                  </div>
                </div>
              </div>
              }
              {this.state.detail == "contact" &&
              <div>
                <div className="details-tabs">
                  <div className="unselected-tab" onClick={() => this.changeTab('services')}> Services </div>
                  <div className="unselected-tab" onClick={() => this.changeTab('costs')}> Costs </div>
                  <div className="selected-tab" > Contact Details </div>
                </div>

                <div className="profile-details-box">
                  <div className="padding-10">

                    <div className="details-row">
                      <img src={ChevronUp} className="detail-chevron"/>
                      <div className="circle-icon small-size"><img className="tick-icon" src={Tick}/></div>
                      <div>
                        <div className="details-heading"> Community Access - Individual Respite </div>
                        <div className="business-description">
                          Community access respite provides activities to encourage a sense of independence and social interaction and provides your carer with a break. This is provided to you individually. 
                        </div>
                       </div>
                    </div>

                    <hr></hr>

                    <div className="details-row">
                      <img src={ChevronDown} className="detail-chevron"/>
                      <div className="circle-icon small-size"><img className="tick-icon" src={Tick}/></div>
                      <div>
                        <div className="details-heading"> In-home Day Respite</div>
                      </div>
                    </div>

                    <hr></hr>

                    <div className="details-row">
                      <img src={ChevronDown} className="detail-chevron"/>
                      <div className="circle-icon small-size"><img className="tick-icon" src={Tick}/></div>
                      <div>
                        <div className="details-heading"> In-home Overnight Respite</div>
                      </div>
                    </div>

                  </div>
                </div>
              </div>
              }
            </div>
            {/* -------------------- End Profile View ----------------- */}
          </div> 
        :
          <div>
            {/* -------------------- Start Search Results ----------------- */}
            <div className="container">
              <p> 
                My Aged Care can help you access support in your home and daily life to help you manage better at home and improve your wellbeing. You may be eligible to receive either individual services or a tailored package of services such as:
              </p>
            </div>

            <div className="blue-section">
                <div className="container">
                  <div className="location-checkbox-row">
                    <div onClick={e => this.filterMethod('Help in the home', !this.state.filters["Help in the home"])}  className="category">
                      { this.state.filters["Help in the home"] && <img className="big-radio" src={RadioActive} /> }
                      { !this.state.filters["Help in the home"] && <img className="big-radio" src={RadioDefault} /> }
                      <div className="white-label"> Help in the home </div>
                    </div>
                    <div onClick={e => this.filterMethod('Aged care homes', !this.state.filters["Aged care homes"])} className="category">
                      { this.state.filters["Aged care homes"] && <img className="big-radio" src={RadioActive} /> }
                      { !this.state.filters["Aged care homes"] && <img className="big-radio" src={RadioDefault} /> }
                      <div className="white-label"> Aged Care Homes </div>
                    </div>

                  </div>
                  <br></br>
                  <div className="search-label"> Search </div>
                  <div className="location-search-row">
                    <input value={this.state.searchProviderValue} onChange={e => this.inputChanged(e)} type="text" name="searchProviderValue" placeholder="What help do you need?" className="location-search-bar"/>
                    <input value={this.state.searchProviderLocation} onChange={e => this.inputChanged(e)} type="text" name="searchProviderLocation" placeholder="Location, Postcode" className="location-search-bar2"/>
                    <button onClick={this.searchProviders} className="service-search-button"> Search </button>
                    <button onClick={this.searchProviders} className="service-search-button-mobile"> <img src={SearchIcon}/> </button>
                  </div>
                </div>
            </div>
            {this.state.isSearching &&
              <div className="spinner-class" >
                <Spinner name="chasing-dots" color="#1F6193"/>
              </div>
            }
            {!this.state.hasSearched ? 
              <div>
                <div className="grey-section">
                  <div className="container">
    
                    <div className="flex-row">

                      <div className="accordian-column">
                        <div className="vertically-center">
                          { this.state.filters[""] && <img className="big-radio" src={CheckActive} /> }
                          { !this.state.filters[""] && <img className="big-radio" src={CheckDefault} /> }
                          Government Subsidised
                        </div>
                        <div className="vertically-center">
                          { this.state.filters[""] && <img className="big-radio" src={CheckActive} /> }
                          { !this.state.filters[""] && <img className="big-radio" src={CheckDefault} /> }
                          Private*
                        </div>
                      </div>
                      <div className="tip-bubble">
                        <h4><i> Tip. </i></h4>
                        <div className="grey-text">To find out which type of care you need</div>
                        <div className="blue-text"> Call Myagedcare on 1800 200 422 </div>
                      </div>
                    </div>
                  <hr></hr>

                  <h3> Narrow my Search </h3>
                  <div className="flex-row">

                    <div onClick={e => this.filterMethod('Home care package', !this.state.filters["Home care package"])} className="vertically-center">
                      { this.state.filters["Home care package"] && <img className="big-radio" src={CheckActive} /> }
                      { !this.state.filters["Home care package"] && <img className="big-radio" src={CheckDefault} /> }
                      Home care package
                    </div>               

                    <div onClick={e => this.filterMethod('Cleaning and laundry', !this.state.filters["Cleaning and laundry"])}  className="vertically-center">
                      { this.state.filters["Cleaning and laundry"] && <img className="big-radio" src={CheckActive} /> }
                      { !this.state.filters["Cleaning and laundry"] && <img className="big-radio" src={CheckDefault} /> }
                      Cleaning and laundry
                    </div>               

                    <div onClick={e => this.filterMethod('Cooking and meals', !this.state.filters["Cooking and meals"])}  className="vertically-center">
                      { this.state.filters["Cooking and meals"] && <img className="big-radio" src={CheckActive} /> }
                      { !this.state.filters["Cooking and meals"] && <img className="big-radio" src={CheckDefault} /> }
                      Cooking and meals
                    </div>               


                    <div onClick={e => this.filterMethod('Food advice', !this.state.filters["Food advice"])}  className="vertically-center">
                      { this.state.filters["Food advice"] && <img className="big-radio" src={CheckActive} /> }
                      { !this.state.filters["Food advice"] && <img className="big-radio" src={CheckDefault} /> }
                      Food preparation
                    </div>               


                    <div onClick={e => this.filterMethod('Nursing', !this.state.filters["Nursing"])}  className="vertically-center">
                      { this.state.filters["Nursing"] && <img className="big-radio" src={CheckActive} /> }
                      { !this.state.filters["Nursing"] && <img className="big-radio" src={CheckDefault} /> }
                      Nursing
                    </div>               


                    <div onClick={e => this.filterMethod('Showering and self-care', !this.state.filters["Showering and self-care"])}  className="vertically-center">
                      { this.state.filters["Showering and self-care"] && <img className="big-radio" src={CheckActive} /> }
                      { !this.state.filters["Showering and self-care"] && <img className="big-radio" src={CheckDefault} /> }
                      Showering and self care
                    </div>               


                    <div onClick={e => this.filterMethod('Transport', !this.state.filters["Transport"])}  className="vertically-center">
                      { this.state.filters["Transport"] && <img className="big-radio" src={CheckActive} /> }
                      { !this.state.filters["Transport"] && <img className="big-radio" src={CheckDefault} /> }
                      Transport
                    </div>               


                    <div onClick={e => this.filterMethod('Health care', !this.state.filters["Health care"])}  className="vertically-center">
                      { this.state.filters["Health care"] && <img className="big-radio" src={CheckActive} /> }
                      { !this.state.filters["Health care"] && <img className="big-radio" src={CheckDefault} /> }
                      Health care
                    </div>               


                    <div onClick={e => this.filterMethod('Respite', !this.state.filters["Respite"])}  className="vertically-center">
                      { this.state.filters["Respite"] && <img className="big-radio" src={CheckActive} /> }
                      { !this.state.filters["Respite"] && <img className="big-radio" src={CheckDefault} /> }
                      Respite
                    </div>               


                    <div onClick={e => this.filterMethod('Social support', !this.state.filters["Social support"])}  className="vertically-center">
                      { this.state.filters["Social support"] && <img className="big-radio" src={CheckActive} /> }
                      { !this.state.filters["Social support"] && <img className="big-radio" src={CheckDefault} /> }
                      Social support
                    </div>               


                    <div onClick={e => this.filterMethod('Home maintenance', !this.state.filters["Home maintenance"])}  className="vertically-center">
                      { this.state.filters["Home maintenance"] && <img className="big-radio" src={CheckActive} /> }
                      { !this.state.filters["Home maintenance"] && <img className="big-radio" src={CheckDefault} /> }
                      Home maintenance
                    </div>               


                    <div onClick={e => this.filterMethod('Aids for mobility, communication and support', !this.state.filters["Aids for mobility, communication and support"])}  className="vertically-center">
                      { this.state.filters["Aids for mobility, communication and support"] && <img className="big-radio" src={CheckActive} /> }
                      { !this.state.filters["Aids for mobility, communication and support"] && <img className="big-radio" src={CheckDefault} /> }
                      Aids for mobility, communication and support
                    </div>               


                    <div onClick={e => this.filterMethod('After-hospital care', !this.state.filters["After-hospital care"])}  className="vertically-center">
                      { this.state.filters["After-hospital care"] && <img className="big-radio" src={CheckActive} /> }
                      { !this.state.filters["After-hospital care"] && <img className="big-radio" src={CheckDefault} /> }
                      After-hospital care
                    </div>               


                    <div onClick={e => this.filterMethod('Complex Needs', !this.state.filters["Complex Needs"])}  className="vertically-center">
                      { this.state.filters["Complex Needs"] && <img className="big-radio" src={CheckActive} /> }
                      { !this.state.filters["Complex Needs"] && <img className="big-radio" src={CheckDefault} /> }
                      Complex Needs
                    </div>               

                  </div>

                  <div onClick={this.searchProviders} className="long-search-button"> Search </div>

                  </div> {/*------- End Container ------*/}

                </div> 
                <div className="container">
                  <h3> An assessment will help you find the services you need: </h3>
                  <div className="white-box">
                    <div className="circle-icon">
                      <div className="white-text"> 1 </div>
                    </div>
                    <div className="grey-text"> Register with Myagedcare </div>
                  </div>

                  <div className="white-box">
                    <div className="circle-icon">
                      <div className="white-text"> 2 </div>
                    </div>
                    <div className="grey-text"> Get an Assessment </div>
                  </div>

                </div>
              </div>

            : 
              <div>
              <br></br>
                <div className="results-flex">
                  <div className="filter-col-30">
                    <FilterColumn filters={this.state.filters} filterMethod={this.filterMethod}/>
                  </div>

                  <div className="results-col-70">
                    {!this.state.searchResults ? "No Results Found.." :
                      <div> 
                        {this.state.searchResults.map(function(item, index) {
                          return (
                            <ResultCard index={index} key={item.Organisation_ID} phone={item.Enquiry_Phone} business={item.Organisation_Name} title={item.Outlet_Name} profileMethod={this.viewProfile}> {item.Description} </ResultCard> 
                          )
                        }, this)}
                      </div>
                    }
                  </div>
                </div>
              </div>
            }
            {/* -------------------- End Search Results ----------------- */}
          </div>
        }

        <Footer/>
    </div>
    );
  }
}

export default Services;



