import React, { Component } from "react";
import ChevronDown from './chevron-arrow-down.svg';
import ChevronUp from './chevron-arrow-up.svg';
import ChevronDown2 from '../Services/chevron-arrow-down.svg';
import ChevronUp2 from '../Services/chevron-arrow-up.svg';

const chevronStyle = {
  position: 'absolute',
  width: '24px',
  right: '10px',
  marginTop: '12px'
}

let buttonRow1 = {
  cursor: 'pointer',
  width: '100%',
  height: '50px',
  backgroundColor: '#154569',
  color: 'white',
  fontSize: '22px',
  paddingLeft: '17px',
  textAlign: 'left'
}

let buttonRow2 = {
  cursor: 'pointer',
  border: 'none',
  width: '100%',
  height: '50px',
  backgroundColor: '#1F6193',
  color: 'white',
  fontSize: '22px',
  paddingLeft: '17px',
  textAlign: 'left'
}

let buttonRow3 = {
  cursor: 'pointer',
  width: '100%',
  height: '50px',
  backgroundColor: '#FFF',
  fontSize: '22px',
  paddingLeft: '17px',
  textAlign: 'left',
  border: 'none',
  color: 'grey',
  borderBottom: '1px solid #F2F2F2'
}

let grey = {
  color: 'grey'
}

let relative = {
  position: 'relative',
  cursor: 'pointer'
}

class Collapsible extends Component {
  show = () => {
    this.setState({isShowing: !this.state.isShowing});
  }

  constructor(props) {
    super(props);

    this.state = {
      isShowing: false
    };
  }
  render() {
    return (
      <div>
        <div style={relative}>
          { (!this.state.isShowing && this.props.style != "white") && <img src={ChevronDown} style={chevronStyle} /> }
          { (this.state.isShowing && this.props.style != "white") && <img src={ChevronUp} style={chevronStyle} /> }
          { (!this.state.isShowing && this.props.style == "white") && <img src={ChevronDown2} style={chevronStyle} /> }
          { (this.state.isShowing && this.props.style == "white") && <img src={ChevronUp2} style={chevronStyle} /> }
        </div>
        {this.props.style != "white" && <button style={this.state.isShowing ? buttonRow2 : buttonRow1} onClick={this.show}> {this.props.title}  </button>}
        {this.props.style == "white" && <button style={this.state.isShowing ? buttonRow3 : buttonRow3} onClick={this.show}> {this.props.title}  </button>}
        {
          this.state.isShowing && <div style={grey}>{this.props.children}</div>
        }
      </div>
    )
  }
}

export default Collapsible;



