import React, { Component } from "react";
import NavBar from '../NavBar/NavBar';
import TopBar from '../TopBar/TopBar';
import Collapsible from '../Collapsible/Collapsible';

import Style from '../style.css';

const headerIMG = {
  backgroundColor: '#154569',
  height: '165px',
  width: '100%',
}

const TopicsContainer = {
  marginTop: '80px',
  display: 'flex'

}
class Topics extends Component {
  render() {
    return (
      <div>
        <TopBar> </TopBar>
        <div className="nav-bar"> <NavBar> </NavBar> </div>
        <div style={headerIMG}>
        </div>
        <div className="container">
          <div className="location-search-row">
            <input type="text" name="topicSearch" placeholder="Topics" className="location-search-bar"/>
            <button className="location-search-button topic-search"> Search </button>
          </div>
          <div style={ TopicsContainer }>
            <div className="categories-column">
              <h4> Categories </h4>
              <br></br>
              <div className="category"> 
                <input className="big-check-box" type="checkbox"/> 
                Registration
              </div>

              <div className="category"> 
                <input className="big-check-box" type="checkbox"/> 
                Assessment
              </div>

              <div className="category"> 
                <input className="big-check-box" type="checkbox"/> 
                In Home Care
              </div>


              <div className="category"> 
                <input className="big-check-box" type="checkbox"/> 
                Aged Care Home
              </div>


              <div className="category"> 
                <input className="big-check-box" type="checkbox"/> 
                Packages
              </div>


              <div className="category"> 
                <input className="big-check-box" type="checkbox"/> 
                Costs
              </div>

              <div className="category"> 
                <input className="big-check-box" type="checkbox"/> 
                Timeframes
              </div>


              <div className="category"> 
                <input className="big-check-box" type="checkbox"/> 
                Costs
              </div>

            </div>
            <div className="accordian-column">
              <Collapsible title="Help in the Home">
                <div className="padded">
                  <p> Before you can access any government funded services, you must have a My Aged Care assessment. An assessment helps to ensure you recieve the care and support you need.</p>

                  <p> An assessment will work out your care needs and what types of care and services you may be eleigible for </p>
                  <p>
                    • Help in the home <br></br>
                    • Respite Care <br></br>
                    • Aged Care Homes <br></br>
                  </p>
                </div>
              </Collapsible> 

              <Collapsible title="Respite Care"> 
                <div className="padded">
                  <p>
                    Respite care can support you and your carer with a break for a short period of time. This gives carers the chance to get to everyday activities or go on a planned break. Respite care may be given informally by family, friends or neighbours, or by formal respite care services.
                  </p>
                  <p>
                    Formal respite care may be for a few hours, days or for longer periods. It can take place in the home, an overnight respite cottage, a day centre or an aged care home.
                  </p>
                  <p>
                    There are different types of respite care to suit your situation depending on your needs, eligibility and what services are available in the area:
                  </p>
                  <p>
                    • community based respite care <br></br>
                    • residential respite care (short stays in aged care homes). 
                  </p>
                </div>
              </Collapsible> 
              <Collapsible title="Aged Care Homes"> 
                <div className="padded">
                  <p>
                    An aged care home (sometimes known as a nursing home) is for older people who can no longer live at home and need ongoing help with everyday tasks or health care. Residential aged care can be permanent or short-term care with help available 24 hours a day. Short-term care in an aged care home is called residential respite care.
                  </p>
                  
                  <p>
                    Privately funded aged care services and housing options, such as retirement villages, are not included on the My Aged Care website as they are not approved providers of aged care services funded by the Australian Government. 
                  </p>

                </div>
              </Collapsible> 
              <Collapsible title="Timeframes"> yooyoyoyooyoy </Collapsible> 
              <Collapsible title="Planning Ahead"> yooyoyoyooyoy </Collapsible> 
              <Collapsible title="Privately Funded Services"> yooyoyoyooyoy </Collapsible> 
              <Collapsible title="End of Life Care"> yooyoyoyooyoy </Collapsible> 
              <Collapsible title="Financial / Costs info"> yooyoyoyooyoy </Collapsible> 
              <Collapsible title="myGov Login"> yooyoyoyooyoy </Collapsible> 
            </div>
          </div>
        </div>
      </div>
    
    );
  }
}

export default Topics;



