import React, { Component } from "react";
import '../NavBar/NavBar.css';

import searchIcon from '../NavBar/search.svg';

import HomeIcon from '../NavBar/icon_1.png';
import Respite from '../NavBar/icon_2.png';
import AgedCare from '../NavBar/icon_3.png';
import Eligibility from '../NavBar/icon_4.png';
import Costs from '../NavBar/icon_5.png';
import Planning from '../NavBar/icon_6.png';
import RightChevron from '../NavBar/right-chevron.svg';
import LeftChevron from '../NavBar/left-chevron.svg';



class MobileNavMenu extends Component {

  constructor(props) {
    super(props);

    this.state = {
      level: 'main' 
    };
  }

  setLevel = (props) => {
    this.setState({level: props}) 
  }


  render() { 
    return (
      <div className="super-container">
        <div className="mobile-nav2">
          <div className="mobile-nav-menu">

            { this.state.level !== 'main' ? "" :
              <div>
                <a href="/">
                  <div className="mobile-nav-item"> 
                    Home
                  </div>
                </a>
                <div className="mobile-nav-item" onClick={() => this.setLevel('AgeCare')}> 
                  About aged care
                  <img src={RightChevron} className="right-chevron"/>
                </div>
                <div className="mobile-nav-item"> 
                  Assessment
                  <img src={RightChevron} className="right-chevron"/>
                </div>
                <a href="/Services">
                  <div className="mobile-nav-item"> 
                    Find a provider
                  </div>
                </a>
                <div className="mobile-nav-item"> 
                  Quicklinks
                  <img src={RightChevron} className="right-chevron"/>
                </div>
              </div>
            }

            { this.state.level !== 'AgeCare' ? "" :
              <div>
                <div className="mobile-nav-item" onClick={() => this.setLevel('main')}> 
                  <img src={LeftChevron} className="left-chevron"/>
                  Back
                </div>
                <div className="mobile-nav-item"> 
                  <img src={HomeIcon} className="mobile-nav-icon"/>
                  Help to stay at home
                </div>
                <div className="mobile-nav-item"> 
                  <img src={Respite} className="mobile-nav-icon"/>
                  Respite and Short Term Care
                </div>
                <div className="mobile-nav-item"> 
                  <img src={AgedCare} className="mobile-nav-icon"/>
                  Aged Care Homes
                </div>
                <div className="mobile-nav-item"> 
                  <img src={Eligibility} className="mobile-nav-icon"/>
                  Eligibility
                </div>
                <div className="mobile-nav-item"> 
                  <img src={Costs} className="mobile-nav-icon"/>
                  Costs
                </div>
                <div className="mobile-nav-item"> 
                  <img src={Planning} className="mobile-nav-icon"/>
                  Planning Ahead
                </div>
                  
              </div>
            }
          </div>
      </div>

    </div>
  
    
    )
  }
}

export default MobileNavMenu;



