import React, { Component } from "react";

import logo from '../../img/logo.png'
import FlatButton from '../FlatButton/FlatButton'

const borderStyle = {
  borderBottom: '1px solid #80808033'
}

class TopBar extends Component {
  render() {
    return (
      <div >
        <div className="above-top-bar">
          <div className="above-top-buttons">
            For Providers
          </div>
          <div className="above-top-buttons">
            Languages
          </div>
        </div>
        <div className="top-bar">
          <img src={logo} alt="Home" className='main-logo'/>
          <div className="top-search-row">
            <input type="text" name="topSearch" className="top-search-bar"/>
            <button className="top-search-button"> Search </button>
          </div>

        </div>
      </div>
    )
  }
}

export default TopBar;



