import React from "react";

const info = {
  fontSize: '12px',
  marginTop: '5px'
}

const description = {
  marginTop: '30px',
  color: 'grey',
  overflow: 'hidden',
  textOverflow: 'ellipsis',
  lineHeight: '16px',
  maxHeight: '32px',
  WebkitLineClamp: '2',
  WebkitBoxOrient: 'vertical'
}


const content = {
  display: 'inline-block',
  width: '60%'
}

const cardHeading = {
  fontSize: '22px',
  color: '#1F6193'
}

const mainCard = {
  padding: '25px',
  position: 'absolute',
  top: '0px'
}

const card = {
  height: '240px',
  position: 'relative',
  border: 'solid 1px #D4D4D4',
  boxShadow: '0 3px 6px rgba(0, 0, 0, 0.16)',
  marginBottom: '25px'
}

const phoneText = {
  margin: '15px',
  marginLeft: '25px',
  fontSize: '22px',
  color: '#1F6193'
}

const blueButton = {
  cursor: 'pointer',
  backgroundColor: '#1F6193',
  borderRadius: '6px',
  color: '#FFF',
  boxShadow: '0 3px 6px rgba(0, 0, 0, 0.16)',
  height: '50px',
  width: '160px',
  marginRight: '30px',
  fontSize: '18px'
}

const flex = {
  display: 'flex',
  justifyContent: 'space-between'
}

const ResultCard = (props) => {

  const click = () => {
    props.profileMethod(props); 
  }
  return (
    <div className="result-card">
      
      { props.imgsrc ? 
        <div>
          <img className="card-image" src={require(`../../img/${props.imgsrc}`)}/>
          <div style={content}>
            <div style={mainCard}>
              <div style={cardHeading}> {props.title} </div>
              <div style={info}> {props.business} </div>
              <div style={description}> {props.children} </div>
            </div> 
            <div className="card-bottom">
              <hr></hr>
              <div style={flex}>
                <div className="result-card-phone" style={phoneText}> Ph. {props.phone} </div>
                <button className="card-button" onClick={click}> View Profile </button>
              </div>
            </div>
          </div>
        </div>
        : 
        <div>
          <div style={mainCard}>
              <div style={cardHeading}> {props.title} </div>
              <div style={info}> {props.business} </div>
              <div style={description}> {props.children} </div>
          </div>
          <div className="card-bottom2">
            <hr></hr>
            <div style={flex}>
              <div className="result-card-phone" style={phoneText}> Ph. {props.phone} </div>
              <button className="card-button" onClick={click}> View Profile </button>
            </div>
          </div>

        </div>

      }

    </div> 
  )
};

export default ResultCard;



